  position: relative;
  .iconfont {
    margin-right: 4px;
  }
  .queryBox {
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    position: relative;
    overflow: inherit;
    .queryBtns {
      float: right;
      margin-top: 14px;
    }
  }
  .content_box {
    margin: 0 15px 0;
    .page_box{
      width: 100%;
      padding: 15px;
      text-align: right
    }
  }
  .changeDetectTableMap {
    height: 100%;
    width: 100%;
    position: absolute;
    right: -120%;
    top:0;
    z-index: 1356;
    background: #fff;
    transition: all 0.8s ease;
    border-radius: 0 0 5px 5px;
    .closeBtn{
      position: absolute;
      right:10px;
      bottom:10px;
      z-index: 131531313;
    }
  }
  .addActive{
    right:0;
  }
}
