




















































































































#tablet{
  position: relative;
  height: 100%;
  overflow: hidden;
  .queryBox {
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    position: relative;
    overflow: inherit;
    line-height: 50px;
    text-align: right;
    .title {
      float: left;
      font-size: 14px;
      i {
        vertical-align: middle;
        font-size: 20px;
        margin-right: 5px;
      }
    }
  }
  .content_box {
    margin: 0 15px 0;
    .page_box{
      width: 100%;
      padding: 15px;
      text-align: right
    }
  }
}
