





















































































































































































































































































































































































#tablet{
  position: relative;
  height: 100%;
  overflow: hidden;
  .queryBox {
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    position: relative;
    overflow: inherit;
    line-height: 50px;
    text-align: right;
    .title {
      float: left;
      font-size: 14px;
      i {
        vertical-align: middle;
        font-size: 20px;
        margin-right: 5px;
      }
    }
  }
  .map-box{
    width: 100%;
    height: calc(100% - 80px);
    padding: 0 15px 15px;
    position: relative;
    .topLeft-box {
      position: absolute;
      top: 0;
      left: 15px;
      width: calc(40% - 20px);
      height: calc(100% - 5px);
      border: 1px solid #ccc;
      /* 地图框样式 */
      #viewDiv{
        width: 100%;
        height: 100%;
      }
    }
    .topRight-box {
      position: absolute;
      top: 0;
      right: 15px;
      width: calc(60% - 20px);
      height: calc(100% - 5px);
      .tab{
        border: 1px solid #ccc;
        height: calc(100% - 35px);
        overflow: auto;
      }
    }
    .bottom-box {
      background: #fff;
      position: absolute;
      top: 0px;
      right: -80%;
      width: calc(60% - 20px);
      height: calc(100% - 40px);
      border: 1px solid #ccc;
      overflow: auto;
      z-index: 100;
    }
    .rightShow{
      right: 15px;
    }
    .sign-btn {
      position: absolute;
      right: 15px;
      bottom: 0px;
      z-index: 99;
    }
    /* box放大 */
    .activeBox, .smallToBig{
      position: absolute;
      left: 15px !important;
      top: 0 !important;
      width: calc(100% - 30px) !important;
      height: 100% !important;
      z-index: 999;
      border: none;
      transition: all 0.8s ease;
      background: #fff;
    }
    .boxBtn {
      position: absolute;
      top: 10px;
      right: 10px;
      z-index: 99;
    }
    .detailsTable {
      height: calc(100% + 65px);
      width: 100%;
      position: absolute;
      right: -120%;
      top: -65px;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .detailsActive {
      right:0;
    }
    .ivu-card >>> .ivu-card-body {
      padding: 0 !important;
    }
  }
}
